<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form action="" v-if="!submited">
    <label>博客标题</label>
    <input type="text" v-model="blog.title" required>
    <label>博客内容</label>
    <textarea name="" id="" cols="30" rows="10" v-model="blog.content"></textarea>
    <div id="checkboxes">
      <label>Vue.js</label>
      <input type="checkbox" value="Vue.js" v-model="blog.description">
      <label>Node.js</label>
      <input type="checkbox" value="Node.js" v-model="blog.description">
      <label>React.js</label>
      <input type="checkbox" value="React.js" v-model="blog.description">
      <label>Angular.js</label>
      <input type="checkbox" value="Angular.js" v-model="blog.description">      
    </div>
    <label>作者</label>
    <select v-model="blog.author">
      <option v-for="author in authors">{{author}}</option>
    </select>
    <button v-on:click.prevent="post">发布博客</button>   
    </form>
    <div v-if="submited">你的博客已经添加！</div>
    <hr>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容</p>
      <p>{{blog.content}}</p>
      <p>博客分类</p>
      <ul>
        <li v-for="description in blog.description">{{description}}</li>
      </ul>
      <p>博客作者</p>
      <p>{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
      blog:{
        title:"",
        content:"",
        description:[],
        author:""
      },
      authors:["dana","jack","mike"],
      submited:false
     
    }
  },
  methods:{
    post:function(){
      this.$http.post("https://vuedemo-1880b.firebaseio.com/posts.json",this.blog)
      .then((data) => {
        console.log(data);
        this.submited=true
      })
    }


  },
  // created(){
  //   this.blog.author="mike"
  // }
}
</script>

<style scoped>
#add-blog *{
  box-sizing: border-box;
}
#add-blog{
  margin: 20px auto;
  padding: 20px;
  max-width: 600px;
}
label{
  display: block;
  margin: 20px 0 10px;
}
input[type="text"],select,textarea{
  display: block;
  padding: 8px;
  width: 100%; 
  margin-bottom: 15px; 
}

#add-blog label{
  display: inline-block;
  margin-top: 0px;
}
#add-blog input{
  display: inline-block;
  margin-right: 10px;
}
button{
  display:block;
  background: crimson;
  margin:20px 0;
  font-size:18px;
  color:#fff;
  padding:15px;
  border-radius: 4px;
}


#preview{
  margin: 30px 0;
  padding: 20px 10px;
  border: 1px dotted #ccc;
}
h3{
  margin-top: 10px;
}



</style>



