<template>
  <div v-theme="'narrow'" id="show-blogs" >
    <h2>博客总览</h2>
    <input type="text" placeholder="搜索" v-model="search">
    <div v-for="blog in filterdeBlogs" class="single-blog">
      <router-link :to="'/blog/'+blog.id">
    	 <h3 v-rainbow>{{blog.title | touppercase}}</h3>
      </router-link>
    	<p>{{blog.content | snippet}}</p>
    </div>
  </div>
</template>

<script>

export default {
  name: 'show-blogs',
  data(){
  	return{
  		blogs:[],
  		search:""
  	}

  },
  created(){
  	// this.$http.get("http://jsonplaceholder.typicode.com/posts")
   //  this.$http.get('../static/posts')
  	
  	// .then(function(data){
  	// 	// console.log(data)
  	// 	this.blogs = data.body.slice(0,10);
  	// 	// console.log(this.blogs) 		
  	// })
    
    
      
    this.$http.get('https://vuedemo-1880b.firebaseio.com/posts.json')
      .then(function(data){
          return data.json();
      })
      .then(function(data){
          var blogsArray = [];
          for (let key in data){
            data[key].id = key;
            blogsArray.push(data[key])
          }
          this.blogs=blogsArray;
          // console.log(this.blogs);
      })
  },
  computed:{
  	filterdeBlogs(){
  		return this.blogs.filter((blog) => {
  			return blog.title.match(this.search)
  		})
  	}
  },
  filters:{
	// 'touppercase':function(value){
	touppercase(value){
		return value.toUpperCase();
	},
	snippet(value){
		return value.slice(0,100) + "...";
	}
  },
  directives:{
  	'rainbow':{
  		bind(el,binding,vnode){
		el.style.color='#'+ Math.random().toString(16).slice(2,8)
		}
  	},
  	'theme':{
  		bind(el,binding,vnode){
			if (binding.value == 'wide'){
			el.style.maxWidth = "1260px";
			}else if(binding.value == 'narrow'){
			el.style.maxWidth = "580px";
			}
		}
  	}
  }

}
</script>

<style>
#show-blogs{
	max-width: 800px;
	margin: 0 auto;
}
.single-blog{
	margin: 20px auto;
	padding: 20px;
	background: #eee;
	box-sizing: border-box;
}
#show-blogs a{
  text-decoration: none;
  color: #;

}
input[type="text"]{
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
</style>



