<template>
  <div id="single-blog">
      <h3>{{blog.title}}</h3>
      <p>{{blog.content}}</p>
      <p>博客分类</p>
      <ul>
        <li v-for="description in blog.description">{{description}}</li>
      </ul>
      <p>博客作者</p>
      <p>{{blog.author}}</p>
  </div>
</template>

<script>

export default {
  name: 'single-blog',
  data(){
  	return{
      id:this.$route.params.id,
  		blog:{},
  	}

  },
  created(){
  	// this.$http.get('http://jsonplaceholder.typicode.com/posts/'+this.id)
  	// .then(function(data){
  	// 	this.blog = data.body;
  	// })
    
    this.$http.get('https://vuedemo-1880b.firebaseio.com/posts/'+ this.id +".json") 
      .then(function(data){
        // console.log(data);
        return data.json();
      })
      .then(function(data){
        this.blog = data;
      })
  }
}
</script>

<style>
#single-blog{
  max-width: 960px;
  margin: 0 auto;
  padding: 20px;
  background: #eee;
  border: 1px dotted #aaa;
}
</style>



